// font-family: 'Film P3', sans-serif;
/* Film P3 - 400 */
@font-face {
  font-family: 'Film P3';
  src: url('../fonts/FilmP3Bold.eot');
  src: local('Film P3 Bold'), local('Film-P3-Bold'),
    url('../fonts/FilmP3Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/FilmP3Bold.woff2') format('woff2'),
    url('../fonts/FilmP3Bold.woff') format('woff'),
    url('../fonts/FilmP3Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

// font-family: 'Times New Roman', serif;
/* Times New Roman - 400 */
@font-face {
  font-family: 'Times New Roman';
  src: url('../fonts/TimesNewRomanMTStd.eot');
  src: local('Times New Roman Regular'), local('Times-New-Roman-Regular'),
    url('../fonts/TimesNewRomanMTStd.eot?#iefix') format('embedded-opentype'),
    url('../fonts/TimesNewRomanMTStd.woff2') format('woff2'),
    url('../fonts/TimesNewRomanMTStd.woff') format('woff'),
    url('../fonts/TimesNewRomanMTStd.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
/* Times New Roman - 700 */
@font-face {
  font-family: 'Times New Roman';
  src: url('../fonts/TimesNewRomanMTStd-Bold.eot');
  src: local('Times New Roman Bold'), local('Times-New-Roman-Bold'),
    url('../fonts/TimesNewRomanMTStd-Bold.eot?#iefix')
      format('embedded-opentype'),
    url('../fonts/TimesNewRomanMTStd-Bold.woff2') format('woff2'),
    url('../fonts/TimesNewRomanMTStd-Bold.woff') format('woff'),
    url('../fonts/TimesNewRomanMTStd-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

